import React from "react";
import { useSelector, useDispatch } from "react-redux";
import * as moment from "moment";
import { getGroupedForcasts, generateForcastsPerDay } from "../store/forecast";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Carousel from "./Carousel";

const useStyles = makeStyles({
  root: {
    maxWidth: 992,
    margin: "0 auto",
    padding: "2rem 10px",
  },
  pad: {
    padding: "10px",
    boxSizing: "border-box",
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginTop: 12,
  },
});

const ForcastsList = ({ metric }) => {
  const dispatch = useDispatch();
  const classes = useStyles();
  const forecasts = useSelector(getGroupedForcasts);

  return (
    <div className={classes.root}>
      <Carousel>
        {Object.keys(forecasts).map((keys) => (
          <div className={classes.pad} key={forecasts[keys][0].dt}>
            <Card
              onClick={() =>
                dispatch(generateForcastsPerDay(forecasts[keys], metric))
              }
            >
              <CardContent>
                <Typography variant="h5" component="h2">
                  Temp:
                  {` ${forecasts[keys][0].main.temp} Â°${
                    metric === "metric" ? "C" : "F"
                  }`}
                </Typography>
                <Typography className={classes.pos} color="textSecondary">
                  {moment(forecasts[keys][0].dt_txt).format("DD MMM YY")}
                </Typography>
              </CardContent>
            </Card>
          </div>
        ))}
      </Carousel>
    </div>
  );
};

export default ForcastsList;
