import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { makeStyles, createStyles } from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
import ForcastsList from "./components/ForcastsList";
import Barchart from "./components/Barchart";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import { loadForcasts } from "./store/forecast";

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      display: "flex",
      height: "100vh",
      alignItems: "center",
      justifyContent: "center",
      "& > * + *": {
        marginLeft: theme.spacing(2),
      },
    },
    center: {
      display: "flex",
      justifyContent: "center",
    },
  })
);

function App() {
  const [value, setValue] = useState("imperial");
  const dispatch = useDispatch();

  const classes = useStyles();
  const forecasts = useSelector((state) => state.forecasts);

  useEffect(() => {
    dispatch(loadForcasts(value));
  }, []);

  const handleRadioChange = (event) => {
    setValue(event.target.value);
    dispatch(loadForcasts(event.target.value));
  };

  return (
    <>
      {forecasts.loading ? (
        <div className={classes.root}>
          <CircularProgress color="secondary" />
        </div>
      ) : (
        <>
          <div className={classes.center}>
            <FormControl component="fieldset">
              <RadioGroup
                row
                aria-label="forcast"
                name="forcast"
                value={value}
                onChange={handleRadioChange}
              >
                <FormControlLabel
                  value="metric"
                  control={<Radio />}
                  label="Celcius"
                />
                <FormControlLabel
                  value="imperial"
                  control={<Radio />}
                  label="Fahrenheit"
                />
              </RadioGroup>
            </FormControl>
          </div>
          <ForcastsList metric={value} />
          <Barchart />
        </>
      )}
    </>
  );
}

export default App;
